@import url('https://fonts.googlrapis.com/css2?family=Roboto:wight@400;500;700;900&display=swap');

*{
    font-family: 'Roboto',sans-serif;
    font-size: 18px;

}
.row{
    padding-top: 5%;
}

.container{
    width: 296px;
    height: 420px;
    text-align: center;
    border: 10px solid #101116;
    background: #101116 ;
    border-radius: 5px;
}
.keypad{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: minmax(60px,auto);
}

input[type='text']{
    height: 75px;
    width: 249px;
    background-color: #10111600;
    border: 0px;
    color: #ffffffff;
    text-align: right;
    font-size: 25px;
    font-weight: 500;
    letter-spacing: 1px;

}

#Clear{
    grid-column: 1/3;
    grid-row: 1;
    color: #262834;

}

#result{
    grid-column: 3/5;
    grid-row: 5;
    color: #262834
}

#backspace{
    color: #262834;
}

button{
    margin: 5px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    background: #262834;
    color: #ffffffff;
    font-weight: 500;
}

input:focus,
input:active,
button:focus,
button:active{
    outline: none;
}

.highlight{
    background: #56cbdb;
}
#box{
    background: #ccc;
    border: 5px dashed #262834;
    height:350px;
}
